<template>
	<div>
		<Row class="container">
			<i-col span="13" offset="2" class="login-img-box">
				<img src="../../static/img/sale.jpg" alt="">
			</i-col>
			<i-col span="7" class="login-box">
				<router-view></router-view>
			</i-col>
		</Row>
		<Footer></Footer>
	</div>
</template>

<script>
	import Footer from './footer/Footer';

	export default {
		name: 'Login',

		data() {
			return {};
		},

		components: {
			Footer
		},

		mounted() {},

		methods: {},
	};
</script>

<style scoped>
	.container {
		margin-top: 30px;
		height: 485px;
		background-color: #fff;
	}

	.login-img-box {
		height: 485px;
		overflow: hidden;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.login-img-box > img {
		width: 68%;
	}

	.login-box {
		height: 485px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
</style>



